      <v-flex md4>
        <v-card class="elevation-1">
          <v-responsive :aspect-ratio="16/9">
            <v-card-text class="text-md-center text-xs-center text-sm-center">
            <v-icon large color="blue darken-2">assignment</v-icon>
            <p class="headline font-weight-light">Rules of the road</p>
            <p>The rules of the road is the most important thing to know when on the road. This short course has got you covered</p>
